<template>
    <div class="income-expense">
        <ul class="cell-content" v-infinite-scroll="requestData" infinite-scroll-disabled="cellLoading" infinite-scroll-distance="0" infinite-scroll-immediate-check="false">
            <li v-for="dataItem in notesArr">
                <income-cell v-if="dataItem.type.toString()=='2'" class="cell" :cell-data="dataItem"></income-cell>
                <expense-cell v-else-if="dataItem.type.toString()=='3'" class="cell" :cell-data="dataItem"></expense-cell>
            </li>
        </ul>
        <empty-bg :msg="emptyMsg" v-show="showEmpty"></empty-bg>
    </div>
</template>

<script>
import { mapState } from "vuex";
import { formatDate } from "../../../common/js/date.js";
import EmptyBg from "../../popupBox/emptyBg.vue";
import incomeCell from "./incomeCell.vue";
import expenseCell from "./expenseCell.vue";
import Vue from "vue";
import { InfiniteScroll } from "mint-ui";
Vue.use(InfiniteScroll);

export default {
  name: "income-expense",
  data() {
    return {
      notesArr: [],
      cellLoading: true,
      curPage: 0,
      pageNum: 10,
      showEmpty: false,
      emptyMsg: "当前没有记录哦~",
      existIdArr: [] //保存已经显示的订单id
    };
  },
  components: {
    incomeCell,
    EmptyBg,
    expenseCell
  },
  mounted() {
    document.body.scrollTop = 0;
    this.showEmpty = false;
    this.requestData(false);
    this.$store.dispatch("toggleLoading", false);
  },
  methods: {
    requestData(infinite = true) {
      if (infinite && this.page == 0) return;
      if (infinite && this.cellLoading) return;
      this.cellLoading = true;
      this.$http
        .post(this.path + `php/suncode/dealfuc.php`, {
          user:this.userid,
          fuc: "moneynotes",
          page: this.curPage
        })
        .then(response => {
          let serverData=response.body;
          //serverData=[{"ids":162,"usetype":3,"useid":41,"userid":181,"nums":"0.00","opdate":"1522745989","userrole":3,"bossname":"\u8c2d\u9e4f\u96c4","username":"\u5927\u5c0f"},{"ids":158,"usetype":2,"useid":40,"userid":181,"nums":"0.00","opdate":"1522745832","userrole":3,"bossname":"\u8c2d\u9e4f\u96c4","username":"\u5927\u5c0f"},{"ids":154,"usetype":2,"useid":39,"userid":181,"nums":"0.03","opdate":"1522397173","userrole":3,"bossname":"\u8c2d\u9e4f\u96c4","username":"Pei"},{"ids":150,"usetype":2,"useid":38,"userid":181,"nums":"0.00","opdate":"1522138104","userrole":3,"bossname":"\u8c2d\u9e4f\u96c4","username":"Pei"},{"ids":146,"usetype":2,"useid":37,"userid":181,"nums":"0.01","opdate":"1522052571","userrole":3,"bossname":"\u8c2d\u9e4f\u96c4","username":"\u5927\u5c0f"},{"ids":142,"usetype":2,"useid":36,"userid":181,"nums":"0.00","opdate":"1521777848","userrole":3,"bossname":"\u8c2d\u9e4f\u96c4","username":"\u5927\u5c0f"},{"ids":138,"usetype":2,"useid":35,"userid":181,"nums":"0.00","opdate":"1521777817","userrole":3,"bossname":"\u8c2d\u9e4f\u96c4","username":"\u5927\u5c0f"},{"ids":134,"usetype":2,"useid":34,"userid":181,"nums":"0.00","opdate":"1521777782","userrole":3,"bossname":"\u8c2d\u9e4f\u96c4","username":"\u5927\u5c0f"},{"ids":129,"usetype":2,"useid":33,"userid":181,"nums":"3.00","opdate":"1521255324","userrole":3,"bossname":"\u9716\u6e6b\uff1a\u5546\u52a1\u652f\u6301\u90e8","username":"Shirley\u9648"},{"ids":124,"usetype":2,"useid":32,"userid":181,"nums":"3.00","opdate":"1521255017","userrole":3,"bossname":"AAA   \u7f8e\u4e1a -\u654f","username":"Monday\u5468\u8001\u5e08"}];
          if (!serverData || serverData.length == 0) {    //数据为空
            this.cellLoading = true;
            if(this.curPage==0) this.showEmpty=true;
          } 
          else {                     //有数据
            if (serverData.length % 10 == 0) {        //满一页数据
              this.curPage++;
              this.cellLoading = false;
            } else{                                        //不满一页
              this.cellLoading = true;
            } 
            let detailsArr = this.analysisData(serverData);
            for (let i = 0; i < detailsArr.length; i++) {
              let orderid = detailsArr[i].ids;
              if (this.existIdArr.indexOf(orderid) >= 0) continue;
              this.notesArr.push(detailsArr[i]);
              this.existIdArr.push(orderid);
            }
          }
        });
    },
    analysisData(data) {
      let detailsArr = [];
      data.forEach((val, index, arr) => {
        let tempDate = new Date(val.opdate * 1000);
        let happenTime = formatDate(tempDate, "yyyy-MM-dd hh:mm");
        detailsArr.push({
          ids:val.ids,
          bossname: val.bossname,
          username: val.username,
          money: val.nums,
          time: happenTime,
          type: val.usetype //2代表分成记录、3代表提现记录
        });
      });
      return detailsArr;
    }
  },
  computed: {
    ...mapState(["path", "userid", "role"])
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.income-expense {
  width: 100%;
  padding-top .3rem;

  .cell {
    background: #fff;
    margin-bottom: .09rem;
    // box-shadow: 0 1px 0px rgba(66, 73, 92, 0.2);
  }
}
</style>



