<template>
    <div class="expense-cell">
        <div class="left">
            <p class="money">{{cellData.money}}<span class="symbol">元</span></p>
            <div class="line"></div>
        </div>
        <div class="right">
          <p class="dec">您提现一次</p>
          <p class="time">{{cellData.time}}</p>
        </div>
    </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "expense-cell",
  data() {
    return {};
  },
  props: {
    cellData: {
      type: Object,
      default: function() {
        return {
          bossname: "",
          username: "",
          money: 0,
          time: ""
        };
      }
    }
  },
  components: {},
  activated() {},
  mounted() {},
  computed: {
    ...mapState(["openid", "path", "role"])
  },
  methods: {}
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.expense-cell {
    width: 100%;
    height: 0.9rem;
    display: flex;
    align-items: center;

    .left {
        flex: 0 0 1.05rem;
        text-align: center;
        position: relative;
        height: 100%;

        .money {
            color: #3dbb7c;
            font-size: 0.18rem;
            letter-spacing: 0.005rem;
            line-height: 0.9rem;

            .symbol {
                font-size: 0.12rem;
                margin-left: 0.01rem;
            }
        }

        .line {
            top: 0;
            position: absolute;
            width: 1px;
            background-color: #f2f2f0;
            height: 100%;
            right: 0;
        }
    }

    .right {
        padding-left: 0.08rem;
        padding-right: 0.08rem;
        box-sizing: border-box;

        .dec {
            color: #303030;
            font-size: 0.14rem;
            font-weight: 600;
            letter-spacing: 0.01rem;
        }

        .time {
            margin-top .08rem;
            color: #7d7d7d;
            font-size: 0.13rem;
            line-height: 0.17rem;
        }
    }
}
</style>



