<template>
    <div class="income-cell">
        <div class="left">
            <p class="money">+{{cellData.money}}<span class="symbol">元</span></p>
            <div class="line"></div>
        </div>
        <div class="right">
          <p class="dec">获得提成</p>
          <p class="light time">{{cellData.time}}</p>
          <p class="light details"><span class="name">{{cellData.username}}&nbsp;</span>在<span class="name">&nbsp;{{incomeName}}&nbsp;</span>的店里绑定了套餐</p>
        </div>
    </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "income-cell",
  data() {
    return {};
  },
  props: {
    cellData: {
      type: Object,
      default: function() {
        return {
          bossname: "",
          username: "",
          money: 0,
          time: ""
        };
      }
    }
  },
  components: {},
  activated() {},
  mounted() {},
  computed: {
    ...mapState(["openid", "path", "role"]),
    incomeName() {
      if (this.role.toString() == "2") {
        return "您";
      } else {
        return this.cellData.bossname;
      }
    }
  },
  methods: {}
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>

.income-cell {
  width: 100%;
  height: 0.9rem;
  display: flex;
  align-items: center;

  .left {
    flex: 0 0 1.05rem;
    text-align: center;
    position: relative;
    height 100%;

    .money {
      color: #a7885c;
      font-size: 0.18rem;
      letter-spacing: 0.005rem;
      line-height 0.9rem;

      .symbol {
        font-size: 0.12rem;
        margin-left: 0.01rem;
      }
    }

    .line {
      top 0;
      position: absolute;
      width: 1px;
      background-color: #f2f2f0;
      height: 100%;
      right 0;
    }
  }

  .right {
    padding-left: 0.08rem;
    padding-right: 0.08rem;
    box-sizing: border-box;

    .dec {
      color: #303030;
      font-size: 0.14rem;
      font-weight: 600;
      letter-spacing: 0.01rem;
    }

    .light {
      color: #7d7d7d;
      font-size: 0.13rem;
      line-height: 0.17rem;

      .name {
        color: #303030;
        font-weight: 500;
      }

      &.time {
        margin-top: 0.06rem;
      }

      &.details {
      }
    }
  }
}
</style>



